.preloader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #000;
    z-index: 9999;
    &__hover {
        content: "";
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        z-index: 9999;
        background-color: #000;
    }
    &__composition {
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate3d(-50%, -50%, 0);
    }
    &__keyhole {
        position: absolute;
        top: 50%;
        left: 50%;
        transform:translate3d(-45%, -18.5%, 0);
        width: 39px;
        height: 71px;
        display: flex;
        svg {
            height: 100%;
            object-fit: contain;
        }
    }
}