body {
    position: relative;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background: #FFF;
    transition: background;
    transition-duration: .3s;
    &.dark-theme {
        background: #000;
    }
}

main {
    position: relative;
}

a {
    text-decoration: none;
    color: inherit;
}

h1, .h1 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 64px;
    letter-spacing: 0.02em;
    color: #F8B017;
    opacity: 1;
    transition: opacity;
    transition-duration: 1.3s;
    &.not-active {
        opacity: .15;
    }
    @media (max-width: 550px) {
        font-size: 38px;
    }
}

h2, .h2 {
    font-family: 'Inter';
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    line-height: 150%;
    letter-spacing: 0.04em;
}

p, .text-big {
    font-family: 'Inter';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.06em;
    @media (max-width: 550px) {
        font-size: 14px;
    }
}

.text-medium {
    font-family: 'Inter';
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.06em;
}

.text-small {
    font-family: 'Inter';
    font-style: normal;
    font-weight: bold;
    font-size: 11px;
    line-height: 140.62%;
    letter-spacing: 0.08em;
    color: #000;
}

h2, .h2,
p, .text-big,
.text-medium,
.text-small {
    transition: color;
    transition-duration: .3s;
    body.dark-theme & {
        color: #D0D0D0;
    }
}

@function to-vh($fraction) {
    $percent: $fraction * 100vh;
    @return $percent;
}

@function to-vw($fraction) {
    $percent: $fraction * 100vw;
    @return $percent;
}