.link {
    &-arrow {
        position: relative;
        padding-right: 45px;
        max-width: max-content;
        // &::after {
        //     content: "";
        //     position: absolute;
        //     width: 25px;
        //     height: 18px;
        //     background: url(../img/arr.svg) center no-repeat;
        //     background-size: contain;
        //     top: 15px;
        //     right: 0;
        //     @media (max-width: 550px) {
        //         width: 18px;
        //         height: 14px;
        //         top: 7px;
        //     }
        // }
        .arr {
            width: 25px;
            height: 18px;
            position: absolute;
            top: 15px;
            right: 0;
            display: inline-block;
            mix-blend-mode: screen;
            background: linear-gradient(225deg, #FFAD00 21%, #DAA520 60%, #FFF 85%, hsla(41, 72%, 76%, 0.986) 72%);
            background-size: 3em 3em, 2em 2em;
            @media (max-width: 550px) {
                top: 4px;
            }

        }
        &:hover {
            color: #808080 !important;
            .arr {
                animation-name: arr;
                animation-duration: 1.5s;
            }
        }
        @media (max-width: 550px) {
            font-size: 15px;
        }
        @keyframes arr {
            from {
                background-position: 100% 0;
            }
            to {
                background-position: -50% 180%;
            }
        }
    }
    &-gold {
        max-width: max-content;
        mix-blend-mode: screen;
        background-image: linear-gradient(-25deg, #FFAD00 21%, #DAA520 60%, #FFF 85%, hsla(41, 72%, 76%, 0.986) 72%);
        background-size: 3em 3em, 2em 2em;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        position: relative;
        padding: 15px 0;
        &::after {
            content: "";
            position: absolute;
            bottom: 5px;
            width: 0;
            height: 2px;
            mix-blend-mode: screen;
            background: linear-gradient(-25deg, #FFAD00 21%, #DAA520 60%, #FFF 85%, hsla(41, 72%, 76%, 0.986) 72%);
            background-size: 3em 3em, 2em 2em;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            left: 0;
            opacity: 1;
            transition: opacity width;
            transition: .3s;
            z-index: 0;
        }
        &:hover {
            animation-name: ani;
            animation-duration: 1s;
            animation-timing-function: linear;
            animation-direction: alternate;
            &::after {
                animation-name: ani;
                animation-duration: 1s;
                animation-timing-function: linear;
                animation-direction: alternate;
                opacity: 1;
                width: 100%;
            }
        }
        @keyframes ani {
            from {
                background-position: 0 0;
            }
            to {
                background-position: 60% 0;
            }
        }
    } 
    &-gradient {
        max-width: max-content;
        position: relative;
        .gradient {
            position: absolute;
            z-index: 1;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(90deg, rgba(255, 255, 255, 0) 31.77%, #FFF 82.29%);
            body.dark-theme & {
                background: linear-gradient(90deg, rgba(0, 0, 0, 0) 23.45%, #000000 75.14%);
            }
        }
    }
    &-social {
        display: inline-block;
        width: 40px;
        height: 40px;
        margin-left: 40px;
        
        span {
            display: block;
            width: 100%;
            height: 100%;
            mix-blend-mode: screen;
            background: linear-gradient(225deg, #FFAD00 21%, #DAA520 60%, #FFF 85%, hsla(41, 72%, 76%, 0.986) 72%);
            background-size: 4em 4em, 2em 2em;
        }
        &:first-child {
            margin-left: 0;
        }
        @media (max-width: 550px) {
            // width: 30px;
            // height: 30px;
            margin: 15px 12px;
        }
        &:hover {
            span {
                animation-name: icon;
                animation-duration: .5s;
            }
        }
        @keyframes icon {
            from {
                background-position: 100% 0;
            }
            to {
                background-position: -50% 180%;
            }
        }
    }
}