.header-container {
    max-width: 1350px;
    margin: 0 auto;
    padding: 50px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
    @media (max-width: 540px) {
        margin-bottom: 0;
        padding-bottom: 20px;
    }
}

.logo {
    display: flex;
    align-items: center;
    &__text {
        margin-left: 21px;
        @media (max-width: 550px) {
            margin-left: 8px;
            font-size: 7px;
        }
    }
    &__img {
        width: 100px;
        height: 100px;
        display: flex;
        background-image: url(../img/logo.svg);
        background-repeat: no-repeat;
        background-size: contain;
        body.dark-theme & {
            background-image: url(../img/logo_black.svg);
            background-repeat: no-repeat;
            background-size: contain;
        }
        img {
            width: 100%;
            object-fit: contain;
        }
        @media (max-width: 550px) {
            width: 70px;
            height: 70px;
        }
    }
    @media (max-width: 550px) {
        max-width: 280px;
    }
}

.theme-switcher {
    width: 40px;
    height: 40px;
    // background: url(../img/th.svg) center no-repeat;
    // background-size: contain;
    cursor: pointer;
    transform: rotate(180deg);
    transition: transform;
    transition-duration: .3s;
    span {
        display: block;
        position: relative;
        width: 100%;
        height: 100%;
        mix-blend-mode: screen;
        background: linear-gradient(225deg, #FFAD00 21%, #DAA520 60%, #FFF 85%, hsla(41, 72%, 76%, 0.986) 72%);
        background-size: 4em 4em, 2em 2em;
        &::after {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            // background: rgba(0, 0, 0, .1);
            z-index: 10;
        }
    }
    body.dark-theme & {
        transform: rotate(0);
    }
    // @media (max-width: 550px) {
    //     width: 30px;
    //     height: 30px;
    // }
    &:hover {
        span {
            animation-name: switch;
            animation-duration: .2s;
        }
    }
    @keyframes switch {
        from {
            background-position: 100% 0;
        }
        to {
            background-position: -70% 180%;
        }
    }
}