.content-container {
    position: relative;
}

.links-block {
    position: fixed;
    left: 16vw;
    top: 26vh;
    z-index: 0;
    .h1 {
        background: radial-gradient(ellipse farthest-corner at center center, #E89D00 25%, #F0CD8B 60%, #E3A115 85%, #E89D00 72%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: #FFAD00;
        margin-block-start: 0;
        margin-block-end: 0;
    }
    @media (max-width: 1680px) {
        left: 12vw;
    }
    @media (max-width: 1440px) {
        left: 5vw;
    }
    @media (max-width: 840px) {
        display: none;
    }
    .gradient {
        position: absolute;
        z-index: 1;
        right: 0;
        top: 0;
        width: 440px;
        height: 100%;
        transition: background;
        transition-duration: .3s;
        background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, #FFF 100%);
        body.dark-theme & {
            background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
        }
    }
}

.content {
    position: relative;
    top: 40px;
    z-index: 1;
    transition: transform;
    transition-duration: .6s;
    padding-left: 40vw;
    @media (max-width: 1280px) {
        padding-left: 32vw;
    }
    @media (max-width: 840px) {
        padding: 0 24px;
    }
    &__title-mobile {
        background: radial-gradient(ellipse farthest-corner at center center, #E89D00 25%, #F0CD8B 60%, #E3A115 85%, #E89D00 72%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        color: #FFAD00;
        font-size: 32px;
        margin-bottom: 50px;
        @media (min-width: 840px) {
            display: none;
        }
    }
    &__text {
        max-width: 805px;
        margin-bottom: 25px;
        margin-right: 20px;
        span {
            background: linear-gradient(-125deg, #FFAD00 21%, #FFE3B3 60%, #F8B017 85%, #FFAD00 72%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            color: #FFAD00;
        }
    }
    &__line {
        margin: 90px 0 100px;
        width: 100%;
        height: 1px;
        background: #808080;
        opacity: 0.4;
    }
    &__link {
        display: block;
        margin-bottom: 50px;
    }
    &__gold-links-block {
        margin-top: 100px;
        position: relative;
        top: -20px;
        a {
            display: block;
            color: #808080;
            margin-bottom: 30px;
        }
    }
    &__socials {
        margin-top: 70px;
        margin-bottom: 55px;
        display: flex;
        position: relative;
        top: 80px;
        justify-content: flex-start;
        @media (max-width: 440px) {
            flex-wrap: wrap;
            justify-content: center;
        }
    }
    .footer-text {
        color: #808080 !important;
        position: relative;
        top: 60px;
    }
}